body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

* {
	list-style-type: none;
	box-sizing: border-box;
	margin: 0;
	padding: none;
	font-size: 10px;
	font-weight: normal;
	text-decoration: none;
}

a {
	color: inherit;
	font-weight: bold;
}

.App {
	text-align: center;
	background-color: #F5F5F5;
}

/*header*/
.App-header {
	background: rgb(255,255,255);
	background: -webkit-linear-gradient(45deg, rgba(255,255,255,1) 0%, rgba(0,0,0,1) 0%, rgba(98,98,98,1) 100%);
	background: linear-gradient(45deg, rgba(255,255,255,1) 0%, rgba(0,0,0,1) 0%, rgba(98,98,98,1) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffffff",endColorstr="#626262",GradientType=1);
	background-size: 200% 100%;
	-webkit-animation: gradient-animation 60s ease infinite;
	animation: gradient-animation 60s ease infinite;
	height: 15vh;
	min-height: 9.5em;
	padding: 2.2em;
	margin-bottom: 2em;
	color: #fff;
}

.App-header__content {
	width: 80em;
	margin: 0 auto;
	text-align: left;
	font-weight: normal;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
}

.App-header__content__title {
	font-size: 2.5em;
	display: inline-block;
}

.App-header__content__logo {
	-webkit-animation: App-logo-spin infinite 20s linear;
	        animation: App-logo-spin infinite 20s linear;
	height: 6em;
	width: 6em;
	background-image: url(/warsawjs-workshop-20-flight-scanner/static/media/plane.406f1a35.svg);
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
	display: inline-block;
	margin-left: 2em;
}

/*body*/
.App-body {
	min-height: 65vh;
}

.App-body__search-view {
	width: 80em;
	margin: 0 auto;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex-direction: column;
	        flex-direction: column;
}

.App-body__search-view__wrapper {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex-direction: row;
	        flex-direction: row;
	font-size: 1.8em;
	margin: 3em 0;
	background-color: #fff;
	border-radius: 2px;
	box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.2), 0px 3px 4px rgba(0, 0, 0, 0.12), 0px 2px 4px rgba(0, 0, 0, 0.14);
	padding: 2em;
}

.App-body__search-view__select-airport {
	display: -webkit-flex;
	display: flex;
	width: 25%;
	margin-right: 1.4em;
}

.App-body__search-view__select-date {
	display: -webkit-flex;
	display: flex;
	width: 18%;
	margin-right: 1.4em;
}

.App-body__search-view__select-passengers {
	display: -webkit-flex;
	display: flex;
	width: 10%;
}

.App-body__search-view__button {
	width: 20%;
}

.App-body__flights-view {
	width: 80em;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex-direction: column;
	        flex-direction: column;
	margin: 0 auto;
}

.App-body__flights-view__button {
	margin: 2em 0;
}

/*footer*/
.App-footer {
	height: 10vh;
	color: #9E9E9E;
	padding: 1em 0;
}
/*keyframes*/
@-webkit-keyframes App-logo-spin {
	from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
	to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes App-logo-spin {
	from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
	to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@-webkit-keyframes gradient-animation {
	0%{background-position:0% 50%}
	50%{background-position:100% 50%}
	100%{background-position:0% 50%}
}
@keyframes gradient-animation {
	0%{background-position:0% 50%}
	50%{background-position:100% 50%}
	100%{background-position:0% 50%}
}

/*media queries*/
@media only screen and (max-width: 600px) {
	.App-body__search-view {
		width: 90vw;
	}

	.App-body__search-view__wrapper {
		-webkit-flex-direction: column;
		        flex-direction: column;
		margin: 1.5em 0;
	}

	.App-body__search-view__select-airport,
	.App-body__search-view__select-date,
	.App-body__search-view__select-passengers {
		width: 100%;
		margin: 1em 0;
	}

	.App-body__search-view__button {
		width: 100%;
	}
}

.Flight__wrapper {
	border-radius: 2px;
	box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.2), 0px 3px 4px rgba(0, 0, 0, 0.12), 0px 2px 4px rgba(0, 0, 0, 0.14);
	min-height: 15em;
	margin-bottom: 1.5em;
	display: -webkit-flex;
	display: flex;
	width: 100%;
	background-color: #fff;
}

.Flight__wrapper:hover {
	box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.2), 0px 3px 14px rgba(0, 0, 0, 0.12), 0px 8px 10px rgba(0, 0, 0, 0.14);
}

.Flight__wrapper__content {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	padding: 2em;
	width: 100%;
}

.Flight__wrapper__content__price-box {
	width: 20%;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center
}

.Flight__wrapper__content__price-box__details {
	width: 70%;
	height: auto;
}

.Flight__wrapper__content__price-box__details__label {
	font-size: 1.6em;
	color: #9E9E9E;
}

.Flight__wrapper__content__price-box__details__price {
	color: #00BCD4;
	margin-top: .3em;
}

.Flight__wrapper__content__price-box__details__price__currency {
	font-weight: normal;
	font-size: 3.5em;
}

.Flight__wrapper__content__price-box__details__price__amount {
	font-weight: bold;
	font-size: 3.5em;
}

.Flight__wrapper__content__price-box__details__info {
	font-size: 1.2em;
	color: #9E9E9E;
}

.Flight__wrapper__content__arrows {
	width: 15%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}

.Flight__wrapper__content__arrows__right,
.Flight__wrapper__content__arrows__left {
	background-image: url(/warsawjs-workshop-20-flight-scanner/static/media/arrow.979d7d9f.svg);
	background-position: center;
	background-repeat: no-repeat;
	background-size: contain;
	width: 80%;
	height: 40%;
	margin: 1em;
}

.Flight__wrapper__content__arrows__left {
	-webkit-transform: rotate(180deg);
	        transform: rotate(180deg);
}

.Flight__wrapper__content__flights {
	width: 45%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	padding: 0 2em;
}

.Flight__wrapper__content__flights__flight {
	width: 100%;
	height: 40%;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-flex-direction: row;
	        flex-direction: row;
}

.Flight__wrapper__content__flights__flight__duration {
	font-size: 1.8em;
	color: #212121;
	display: inline-block;
	margin-right: 1em;
	width: 50%;
	text-align: right;
}

.Flight__wrapper__content__flights__flight__changes {
	font-size: 1.4em;
	color: #9E9E9E;
	display: inline-block;
	width: 50%;
	text-align: left;
}

.Flight__wrapper__content__flights__button {
	width: 15%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
}

